*,
*:before,
*:after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Myriad Pro";
  src: url("../fonts/MyriadProRegular/MyriadProRegular.eot");
  src: url("../fonts/MyriadProRegular/MyriadProRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/MyriadProRegular/MyriadProRegular.woff") format("woff"), url("../fonts/MyriadProRegular/MyriadProRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

body {
  margin: 0;
  background: url("../assets/background.png") 100%, no-repeat;
  background-size: cover;
}

.head-banners {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  min-width: 320px;
  height: auto;
  margin: 0 auto;
  padding-top: 62px;
}

.head-banners__item {
  display: inline-block;
  max-width: 237px;
  border-top: 2px solid #ed3000;
  margin: 0 2px 40px;
  text-align: center;
  background-color: #ffffff;
}

.head-banners__item:hover {
  box-shadow: 0 0 49px 2px rgba(0, 0, 0, 0.27);
  transform: scale(1.2);
  transition: 0.7s;
}

.head-banners__item:hover h2 {
  color: #ed3000;
  transition: 1s;
}

.head-banners__item:before {
  display: block;
  position: relative;
  content: ".";
  width: 50px;
  height: 50px;
  color: #ed3000;
  margin-top: -30px;
  margin-left: calc(50% - 25px);
  padding-top: 35px;
  background-color: #ed3000;
  border-radius: 50%;
}

@media all and (max-width: 710px) {
  .head-banners__item {
    max-width: 235px;
  }
}

.head-banners__button-two {
  margin: 10px 0 27px;
  padding: 11px 28px 12px;
  text-shadow: 0px -1px 0px rgba(205, 205, 205, 0.004);
  color: #303030;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 11px;
  font-weight: 400;
  text-transform: uppercase;
  box-shadow: 0 1px 0 #cdcdcd;
  border: none;
  outline: none;
  border-radius: 2px;
  background: #ffffff linear-gradient(to top, rgba(102, 102, 102, 0.25) 0%, rgba(205, 205, 205, 0.25) 100%);
}

.head-banners__button-two:hover {
  text-shadow: 0 -1px 0 #b82200;
  color: #ffffff;
  box-shadow: 0 1px 0 #9a9a9a;
  background: #ed3000 linear-gradient(to top, rgba(205, 205, 205, 0.3) 0%, rgba(255, 255, 255, 0.3) 100%);
  transition: 0.5s;
}

.common-title-style {
  margin: 21px 0 27px;
  color: #666666;
  font-family: "Myriad Pro", sans-serif;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
}

.common-title-style.-whitecolor {
  color: #ffffff;
  padding: 0 79px 23px 24px;
}

@media all and (max-width: 940px) {
  .common-title-style.-whitecolor {
    padding: 0 79px 0 24px;
  }
}

.common-description-style {
  color: #333333;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 12px;
  font-weight: 400;
  padding: 0 28px;
  outline: none;
}

.common-description-style.-main {
  padding: 0;
}

.common-description-style.-color {
  color: #ffffff;
  padding-left: 24px;
}

.grid-container {
  display: grid;
  margin: 0 auto;
  padding-bottom: 41px;
  max-width: 960px;
  grid-template-columns: repeat(4, minmax(230px, 1fr));
  grid-template-rows: repeat(2, 240px);
  grid-auto-flow: column;
  /*   svg figure   */
}

.grid-container__item {
  background-color: #000000;
}

.grid-container__item.-background-color {
  background-color: #ffffff;
  padding: 0 24px;
}

.grid-container__item h4 {
  color: #303030;
  font-family: "Source Sans Pro Semibold", sans-serif;
  font-size: 12px;
  text-transform: uppercase;
}

.grid-container__item:first-child {
  border-bottom: 1px solid #FF0000;
}

.grid-container__item:nth-child(4) {
  border-right: 1px solid #FF0000;
}

.grid-container__item:nth-child(5) {
  border-left: 1px solid #FF0000;
}

.grid-container__item:nth-child(5):after {
  display: block;
  position: absolute;
  content: '';
  width: 2px;
  height: 28px;
  background-color: #ffffff;
  margin-top: -29px;
  margin-left: -1px;
}

.grid-container__item:last-child {
  grid-row: 1 / -1;
  background-color: #ed3000;
}

.grid-container__button-one {
  margin-top: 29px;
  margin-left: 25px;
  padding: 10px 57px;
  text-shadow: 0px -1px 0px rgba(255, 255, 255, 0.004);
  color: #303030;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 13px;
  font-weight: 400;
  text-transform: uppercase;
  box-shadow: 0 1px 0 #b40000;
  border: none;
  border-radius: 2px;
  outline: none;
  background: linear-gradient(to top, rgba(205, 205, 205, 0.25) 0%, rgba(255, 255, 255, 0.25) 100%) #ffffff;
}

.grid-container__button-one:hover {
  transition: 0.8s;
  background-image: radial-gradient(rgba(240, 235, 255, 0.69) 20px, #9696a1 80%);
  color: #888888;
}

.grid-container .triangle-svg-top {
  display: block;
  width: 30px;
  height: 240px;
  padding-top: 225px;
  margin: 0 auto;
}

.grid-container .triangle-svg-left {
  display: block;
  width: 16px;
  height: 30px;
  float: right;
  margin-top: 105px;
  margin-right: -3px;
  clear: both;
}

.grid-container .triangle-svg-right {
  display: block;
  width: 16px;
  height: 30px;
  margin-top: 105px;
  margin-left: -1px;
}

.grid-container .triangle-svg-bottom {
  display: none;
  width: 30px;
  height: 16px;
  margin: 0 auto;
}

@media all and (max-width: 940px) {
  .grid-container {
    max-width: 720px;
    grid-template-columns: repeat(3, minmax(230px, 1fr));
    grid-template-rows: repeat(3, 240px);
  }

  .grid-container__item:last-child {
    grid-row: 3;
    grid-column: 1 / -1;
  }
}

@media all and (max-width: 710px) {
  .grid-container {
    max-width: 480px;
    grid-template-columns: repeat(2, minmax(230px, 1fr));
    grid-template-rows: repeat(4, 240px);
  }

  .grid-container__item:nth-child(3) {
    grid-row: 1;
    grid-column: 2;
  }

  .grid-container__item:nth-child(4) {
    grid-row: 3;
    grid-column: 1;
  }

  .grid-container__item:nth-child(5) {
    border-left: none;
    border-top: 1px solid #FF0000;
  }

  .grid-container__item:nth-child(5):after {
    width: 28px;
    height: 2px;
    margin-top: -17px;
    margin-left: 106px;
    background-color: #ffffff;
  }

  .grid-container__item:last-child {
    grid-row: 4;
    grid-column: 1 / -1;
  }

  .grid-container .triangle-svg-right {
    display: none;
  }

  .grid-container .triangle-svg-bottom {
    display: block;
  }
}

@media all and (max-width: 478px) {
  .grid-container {
    max-width: 320px;
    grid-template-columns: repeat(4, minmax(80px, 1fr));
    grid-template-rows: 240px 240px 240px 260px;
    padding-bottom: 0;
  }

  .grid-container__item:first-child {
    grid-column: 1/2;
  }

  .grid-container__item:nth-child(2) {
    grid-column: 1/4;
  }

  .grid-container__item:nth-child(3) {
    grid-column: 2/5;
  }

  .grid-container__item:nth-child(4) {
    grid-column: 1/2;
  }

  .grid-container__item:nth-child(5) {
    grid-column: 4/5;
  }

  .grid-container__item:nth-child(5):after {
    margin-left: 26px;
    height: 4px;
    margin-top: -18px;
  }

  .grid-container__item:nth-child(6) {
    grid-column: 2/5;
  }

  .grid-container__button-one {
    margin-top: 10px;
  }
}

@charset "UTF-8";

.date-container {
  display: block;
  max-width: 900px;
  margin: 0 auto;
}

.date-container__description {
  display: inline-block;
  position: relative;
  width: 380px;
  padding: 19px;
  text-align: right;
  border-radius: 10px;
  border: 2px solid #e5e5e5;
  background-color: #ffffff;
}

.date-container__description h2 {
  margin: 0;
  padding-bottom: 10px;
  color: #1a1a1a;
  font-family: Raleway, sans-serif;
  font-size: 26px;
  font-weight: 400;
}

.date-container__description p {
  margin: 0;
  opacity: 0.8;
  color: #777777;
  font-family: Lato, sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -0.42px;
}

.date-container__description:first-child {
  top: 50px;
}

.date-container__description:first-child:after {
  display: block;
  position: absolute;
  content: '▶';
  width: 9px;
  height: 9px;
  color: #e5e5e5;
  margin-top: -68px;
  margin-left: 357px;
}

.date-container__description:last-child {
  top: -70px;
  text-align: left;
}

.date-container__description:last-child:before {
  display: block;
  position: absolute;
  content: '◀';
  width: 9px;
  height: 9px;
  color: #e5e5e5;
  margin-top: 55px;
  margin-left: -33px;
}

.date-container__significant-years {
  display: inline-block;
  width: 64px;
  height: 352px;
  margin-left: 60px;
  border-left: 2px dotted rgba(0, 0, 0, 0.2);
}

.date-container__year {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
  border: 2px solid #e6e6e6;
  border-radius: 50%;
  background-color: #ffffff;
  margin-top: 42px;
  margin-left: -40px;
  margin-bottom: 30px;
}

.date-container__year time {
  display: block;
  color: #1a1a1a;
  font-family: Raleway, sans-serif;
  font-size: 26px;
  font-weight: 500;
  opacity: 0.5;
  letter-spacing: -0.78px;
  padding: 20px 10px;
}

@media all and (max-width: 920px) {
  .date-container {
    max-width: 480px;
    margin-left: 6%;
  }

  .date-container__description:first-child {
    top: 170px;
  }

  .date-container__description:first-child:after {
    margin-left: -23px;
    transform: rotate(60deg);
  }

  .date-container__description:last-child {
    top: -170px;
    float: left;
  }

  .date-container__significant-years {
    float: left;
    margin-left: 30px;
  }

  .date-container__year {
    margin-bottom: 90px;
  }
}

@media all and (max-width: 530px) {
  .date-container {
    margin: 0 auto;
  }

  .date-container__significant-years {
    width: 54px;
    margin-left: 40px;
  }
}

@media all and (max-width: 478px) {
  .date-container {
    max-width: 320px;
  }

  .date-container__description {
    width: 320px;
  }

  .date-container__description:first-child {
    top: 50px;
  }

  .date-container__description:first-child:after {
    display: none;
  }

  .date-container__description:last-child {
    top: -100px;
  }

  .date-container__description:last-child:before {
    display: none;
  }

  .date-container__significant-years {
    border-left: none;
    margin-top: -150px;
  }

  .date-container__year {
    height: auto;
    position: absolute;
    left: calc(50% - 40px);
    margin: 0;
    border-radius: 10px;
    z-index: 2;
  }

  .date-container__year time {
    padding: 0;
    text-align: center;
  }

  .date-container__year:last-child {
    margin-top: 235px;
    left: calc(50% - 40px);
  }
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@charset "UTF-8";

/* Slider */

.slick-loading .slick-list {
  background: #fff  center center no-repeat;
}

/* Icons */

@font-face {
  font-family: 'slick';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/slick-fonts/slick.eot");
  src: url("../fonts/slick-fonts/slick.eot?#iefix") format('embedded-opentype'), url("../fonts/slick-fonts/slick.woff") format('woff'), url("../fonts/slick-fonts/slick.ttf") format('truetype'), url("../fonts/slick-fonts/slick.svg#slick") format('svg');
}

/* Arrows */

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto;
}

.slick-prev:before {
  content: '←';
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: -25px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:before {
  content: '→';
}

[dir='rtl'] .slick-next:before {
  content: '←';
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  top: 65px;
  bottom: -25px;
  left: 22px;
  display: block;
  width: 100%;
  height: 15px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left;
}

@media all and (max-width: 940px) {
  .slick-dots {
    top: 25px;
  }
}

.slick-dots li {
  position: relative;
  display: inline-block;
  /*width: 20px;*/
  /*height: 20px;*/
  /*margin: 0 5px;*/
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 14px;
  height: 14px;
  padding: 1px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  font-family: 'slick';
  font-size: 9px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  content: '•';
  text-align: center;
  opacity: .5;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  opacity: 1;
  color: white;
}